import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import Form from '../form';
import FormInput from '../form-input';
import FormButton from '../form-button';
import { useLocation, useNavigate } from 'react-router-dom';

function Register({ onSubmit, onNavigateLogin }) {
    const { register, handleSubmit, formState: { errors } } = useForm({ mode: "onBlur" })

    const emailRegister = register('email', {
        required: {
            value: true,
            message: ' '
        },
        pattern: {
            value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            message: "Email     "
        }
    })

    const passwordRegister = register('password', {
        required: {
            value: true,
            message: ' '
        },
        pattern: {
            value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
            message: "     ,       "
        }
    })

    const groupRegister = register('group', {
        required: {
            value: true,
            message: ' '
        },
        pattern: {
            value: /^group-[0-9]{1,3}$/,
            message: "    group-"
        }
    })
    return (

        <Form title="" handleFormSubmit={handleSubmit(onSubmit)}>
            <FormInput
                {...emailRegister}
                id="email"
                type="text"
                placeholder="email"
            />
            {errors?.email && <p className="errorMessage">{errors?.email?.message}</p>}

            <FormInput
                {...groupRegister}
                id="group"
                type="text"
                placeholder="Id    group- "
            />
            {errors?.group && <p className="errorMessage">{errors?.group?.message}</p>}

            <FormInput
                {...passwordRegister}
                id="password"
                type="password"
                placeholder=""
            />
            {errors?.password && <p className="errorMessage">{errors?.password?.message}</p>}




            <p className="infoText">  ,             .</p>

            <FormButton type="submit" color="pramary"></FormButton>
            <FormButton type="button" color="secondary" onClick={onNavigateLogin}></FormButton>
        </Form>

    );
}

export default Register;